.home {
  padding: 40px;
}

.office-pic {
  width: 100%;
  height: 100%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.main-section {
  height: 300px;
  display: flex;
  margin-bottom: 20px;
}

.main-photo {
  width: 40%;
  border-radius: 10px;
  box-shadow: 0 2px 4px 0 rgb(212, 210, 210), 0 3px 8px 0 rgb(212, 210, 210);
}

.main-info {
  width: 60%;
  display: flex;
  align-items: center;
  font-size: 18px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgb(212, 210, 210), 0 3px 8px 0 rgb(212, 210, 210);
}

.info-box {
  padding: 60px;
}

.section-content {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  gap: 20px;
}

.box1 {
  /* border: 1px solid chartreuse; */
  align-self: start;
  min-height: 330px;
  display: flex;
  padding: 15px;
  flex-direction: column;
  gap: 20px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgb(212, 210, 210), 0 3px 8px 0 rgb(212, 210, 210);
}

.box2 {
  /* border: 1px solid rgb(255, 255, 0); */
  background-color: rgb(250, 248, 248);
  display: flex;
  flex-direction: column;
}

.box3 {
  min-height: 330px;
  flex-direction: column;
  display: flex;
  gap: 20px;
  align-self: start;
  padding: 15px;
  /* border: 1px solid rgb(177, 9, 152); */
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgb(212, 210, 210), 0 3px 8px 0 rgb(212, 210, 210);
}

.home-box-spec {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.box-input-opinion {
  padding: 0 10px;
  border-radius: 15px;
  background-color: fff;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 4px 0 rgb(212, 210, 210), 0 3px 8px 0 rgb(212, 210, 210);
}

.opinion-box {
  height: 100%;
  padding: 15px 15px 0;
  margin-top: 20px;
  /* border: 1px solid black; */
  background-color: #fff;
  /*  display: flex;
  justify-content: center;
  align-items: center; */
  box-shadow: 0 2px 4px 0 rgb(212, 210, 210), 0 3px 8px 0 rgb(212, 210, 210);
}

