  border: 2px solid red;
} */

.person-pic {
  height: 40px;
  width: 40px;
  margin-right: 20px;
}

.ramka1 {
  font-size: 23px;
}

.ramka2 {
  padding: 20px;
  font-size: 16px;
  border-bottom: 1px solid rgb(237, 235, 235);
}

.cc {
  padding: 15px;
}

.btn-send {
  height: 30px;
  width: 60px;
  border-radius: 15px;
  color: #fff;
  background-color: rgb(158, 83, 229);
  border: none;
  cursor: pointer;
}

.input-search {
  border: none;
  &:focus {
    border: none;
  }
}

.swiper-horizontal {
  height: 100%;
}
.swiper-pagination-bullet {
  background-color: #fff;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: rgb(158, 83, 229);
}

.size {
  font-weight: bold;
}

.mm0 {
  height: 40px;
  display: flex;
  margin-bottom: 10px;
  margin-top: 15px;
}

.pic {
  height: 20px;
  width: 20px;
}

.link-tools {
  display: flex;
  gap: 10px;
  align-items: center;
}

.pic-tools {
  height: 20px;
  width: 20px;
}

.departments-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.tools-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.tools-item {
  /* border: 1px solid rgb(47, 127, 255);
  height: 40px; */
  display: flex;
}
