.login-container {
  width: 360px;
  padding: 30px;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-login {
  width: 100%;
  padding: 15px;
 /*  margin: 5px 0px; */
  outline: none;
  background-color: transparent;
}

.input-login:focus {
  box-shadow: 0 2px 2px 0  rgba(109, 17, 195, 0.212), 0 3px 4px 0  rgba(109, 17, 195, 0.212); 
}

.btn-box {
  width: 100%;
  display: flex;
  justify-content: center;
}

.btn-login {
  margin-top: 20px;
  width: 110px;
  height: 35px;
  /* margin: 0 10px; */
  background: linear-gradient(to right, #5606d6, hsl(292, 72%, 56%));
  border-radius: 30px;
  border: 0;
  outline: none;
  color: #fff;
  cursor: pointer;
}

.btn-login:hover {
  background: rgb(158, 83, 229);
}

.background-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  min-height: 100vh;
  background-color: rgb(109, 17, 195);
}

.background-container .background-box {
  width: 325px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.background-container .background-box .bubbles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: hidden;
  top: 0;
  left: 0;
}
.background-container .background-box .bubble {
  position: absolute;
  bottom: -100px;
  width: 40px;
  height: 40px;
  background: #f1f1f1;
  border-radius: 50%;
  opacity: 0.5;
  -webkit-animation: rise 10s infinite ease-in;
          animation: rise 10s infinite ease-in;
}
.background-container .background-box .bubble:nth-child(1) {
  width: 40px;
  height: 40px;
  left: 10%;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
}
.background-container .background-box .bubble:nth-child(2) {
  width: 20px;
  height: 20px;
  left: 20%;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.background-container .background-box .bubble:nth-child(3) {
  width: 50px;
  height: 50px;
  left: 35%;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.background-container .background-box .bubble:nth-child(4) {
  width: 80px;
  height: 80px;
  left: 50%;
  -webkit-animation-duration: 11s;
          animation-duration: 11s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
.background-container .background-box .bubble:nth-child(5) {
  width: 35px;
  height: 35px;
  left: 55%;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.background-container .background-box .bubble:nth-child(6) {
  width: 45px;
  height: 45px;
  left: 65%;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.background-container .background-box .bubble:nth-child(7) {
  width: 90px;
  height: 90px;
  left: 70%;
  -webkit-animation-duration: 12s;
          animation-duration: 12s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.background-container .background-box .bubble:nth-child(8) {
  width: 25px;
  height: 25px;
  left: 80%;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
}
.background-container .background-box .bubble:nth-child(9) {
  width: 15px;
  height: 15px;
  left: 70%;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
.background-container .background-box .bubble:nth-child(10) {
  width: 90px;
  height: 90px;
  left: 25%;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
@-webkit-keyframes rise {
  0% {
    bottom: -100px;
    transform: translateX(0);
  }
  50% {
    transform: translate(100px);
  }
  100% {
    bottom: 1080px;
    transform: translateX(-200px);
  }
}
@keyframes rise {
  0% {
    bottom: -100px;
    transform: translateX(0);
  }
  50% {
    transform: translate(100px);
  }
  100% {
    bottom: 1080px;
    transform: translateX(-200px);
  }
}
